import React from "react";
import BrandCard from "./BrandCard";
import SubTitle from "../Uitlity/SubTitle";
import brand1 from "../../images/brand1.png";
import brand2 from "../../images/brand2.png";
import brand3 from "../../images/brand3.png";

const BrandCounter = ({title, btntitle}) => {
  return (
    <div className="containerXl lg:max-[1100px]:px-6 md:max-[992px]:px-6 max-sm:px-6">
      <SubTitle title={title} btntitle={btntitle} pathText="/allbrand" />
      <div className="grid max-md:grid-cols-2 md:grid-cols-3 lg:grid-cols-6 pb-4 gap-6">
        <BrandCard img={brand1} />
        <BrandCard img={brand2} />
        <BrandCard img={brand3} />
        <BrandCard img={brand1} />
        <BrandCard img={brand2} />
        <BrandCard img={brand3} />
      </div>
    </div>
  );
};

export default BrandCounter;
