import React from "react";
import { Link } from "react-router-dom";
import rate from "../../images/rate.png";

const AdminAllProductsCard = ({ img }) => {
  return (
    <div className="rounded-lg bg-white shadow">
      <div className="between px-2 py-1 gray">
        <a href="">إزالة</a>
        <a href="">تعديل</a>
      </div>
      <Link to="/products/:id">
        <img src={img} className="w-full h-56" />
      </Link>
      <div className="p-3">
        <p className="text-[#555550] text-sm mb-2">لابتوب HP</p>
        <div className="between">
          <div className="flex gap-2">
            <img src={rate} className="w-4 h-4" />
            <span className="text-[#ffc107] text-sm font-bold">4.5</span>
          </div>
          <div className="font-bold text-lg">800 $</div>
        </div>
      </div>
    </div>
  );
};

export default AdminAllProductsCard;
