import React from "react";
import rate from "../../images/rate.png";

const RateItem = () => {
  return (
    <div>
      <div className="flex items-center gap-[6px] pt-4 mx-10">
        <div className="font-semibold text-base text-[#555550]">
          أحمد محمود
        </div>
        <img src={rate} className="w-4 h-4" />
        <span className="text-[#ffc107] text-sm font-semibold">4.3</span>
      </div>
      <div className="border-b mt-1">
        <div className="text-sm pb-2 mx-10 text-[#555550]">
          منتج مناسب سعره للوقت الحالي وجه كويس جدا ومعاه دراع زيادة
        </div>
      </div>
    </div>
  );
};

export default RateItem;
