import React from "react";
import next from "../../images/next.png";
//

const LeftButton = (onClick, onDisable) => {
  return (
    <i
      className="fa-solid fa-chevron-left float-left mt-52 cursor-pointer w-9 h-9 bg-slate-100 rounded-full center"
      onClick={onClick}
      onDisable={onDisable}
    ></i>
  );
};

export default LeftButton;
