import React from "react";
import avatar from "../../images/avatar.png";

const AdminAddBrand = () => {
  return (
    <div className="grid grid-cols-12">
      <div className="font-extrabold text-[22px] text-[#555550] mb-5 col-span-12">
        أضف ماركة جديدة
      </div>
      <div className="col-span-8 max-sm:col-span-12">
        <p className="gray mb-2">صورة الماركة</p>
        <img src={avatar} className="w-32 mb-4" />
        <input
          type="text"
          placeholder="اسم الماركة"
          className="p-2 rounded-lg bg-gray-100 border border-gray-300 w-full mb-2"
        />
        <div className="flex justify-end w-full">
          <button className="duration-500 text-white hover:text-[#555550] bg-black py-3 px-6 rounded-lg">
            حفظ التعديلات
          </button>
        </div>
      </div>
    </div>
  );
};

export default AdminAddBrand;
