import React from 'react';
import { Link } from "react-router-dom";

const LoginPage = () => {
  return (
    <div style={{minHeight:"670px"}} className="containerXl flex justify-center mt-12">
      <form className="flex flex-col">
        <label className="text-[#555550] font-extrabold text-2xl mx-auto">تسجيل الدخول</label>
        <input type="email" className="bg-white w-96 h-9 mt-4 mx-auto rounded-lg border border-[gray] text-center" placeholder="الايميل..." />
        <input type="password" className="bg-white w-96 h-9 mt-5 mx-auto rounded-lg border border-[gray] text-center" placeholder="كلمة السر..." />
        <input type="submit" className="bg-zinc-900 text-white hover:text-gray-300 duration-300 p-3 w-96 mt-5 mx-auto rounded-lg text-center cursor-pointer" value="تسجيل الدخول" />
        <label className="mx-auto mt-6">ليس لديك حساب ؟<Link to="/register" className="text-red-500"> اضغط هنا</Link>
        </label>
        <label className="mx-auto mt-6"><Link to="/admin/allproducts" className="text-blue-500">الدخول بحساب الأدمن</Link>
        </label>
        <label className="mx-auto mt-6"><Link to="/user/allorders" className="text-blue-500">الدخول بحساب المستخدم</Link>
        </label>
      </form>
    </div>
  )
}

export default LoginPage;
