import React from "react";
import favOff from "../../images/favOff.png";
import rate from "../../images/rate.png";
import { Link } from "react-router-dom";

const ProductCard = ({img}) => {
  return (
    <div className="rounded-lg bg-white shadow">
      <Link to="/products/:id"><img src={img} className="w-full h-56"/></Link>
      <div className="flex justify-end mx-2">
        <img src={favOff} className="w-7 h-7" />
      </div>
      <div className="p-3">
        <p className="text-[#555550] text-sm mb-2">لابتوب HP</p>
        <div className="between">
          <div className="flex gap-2">
            <img src={rate} className="w-4 h-4" />
            <span className="text-[#ffc107] text-sm font-bold">4.5</span>
          </div>
          <div className="font-bold text-lg">800 $</div>
        </div>
      </div>
    </div>
  );
};

export default ProductCard;
