import React from "react";

const RightButton = (onClick, onDisable) => {
  return (
    <i
      className="fa-solid fa-chevron-right float-right mt-52 cursor-pointer w-9 h-9 bg-slate-100 rounded-full center"
      onClick={onClick}
      onDisable={onDisable}
    ></i>
  );
};

export default RightButton;
