import React from "react";
import logo from "../../images/logo.png";
import login from "../../images/login.png";
import cart from "../../images/cart.png";

const NavBarLogin = () => {
  return (
    <div className="bg-zinc-900 p-4">
      <div className="containerXl flex items-center gap-3">
        <a href="/">
          <img src={logo} className="w-11 h-11"></img>
        </a>
        <input
          type="search"
          placeholder="ابحث.."
          className="flex flex-1 p-2 rounded-md text-center outline-none focus:outline-4 focus:outline-blue-700 focus:outline-offset-0"
        ></input>
        <div className="flex gap-2 max-sm:flex-col">
          <a href="/login" className="flex items-center">
            <img src={login} className="w-5 h-5"></img>
            <p className="text-white mr-1 text-[13px]">دخول</p>
          </a>
          <a href="/cart" className="flex items-center">
            <img src={cart} className="w-5 h-5"></img>
            <p className="text-white mr-1 text-[13px]">العربة</p>
          </a>
        </div>
      </div>
    </div>
  );
};

export default NavBarLogin;
