import React from "react";
import mobile from "../../images/mobile.png";
import { Link } from "react-router-dom";

const CartItem = () => {
  return (
    <div className="grid grid-cols-12 px-2 mb-3 bg-white rounded-2xl">
        <img
          src={mobile}
          className="w-36 h-48 col-span-2 max-lg:col-span-3 max-sm:col-span-4 cursor-pointer"
        />
      <div className="col-span-10 max-lg:col-span-9 max-sm:col-span-8 pb-5">
        <div className="between pt-2 mb-5">
          <div className="text-xs gray font-semibold">الالكترونيات</div>
          <div className="flex items-center cursor-pointer">
            <i class="fa-regular fa-trash-can"></i>
            <span className="text-xs gray mr-3">إزالة</span>
          </div>
        </div>
        <span className="text-sm text-[#555550] ml-2">
          آيفون XR بذاكرة سعة 128 جيجابايت ويدعم تقنية 4G LTE مع تطبيق فيس
        </span>
        <span className="text-[#ffc107] text-sm font-bold">4.5</span>
        <div className="my-1">
          <span className="gray text-xs font-semibold mb-2 ml-1">
            الماركة :
          </span>
          <span className="text-[#555550] text-lg font-semibold">ابل</span>
        </div>
        <div className="bg-red-500 w-8 h-8 rounded-full cursor-pointer border border-slate-200"></div>
        <div className="between">
          <div className="my-2">
            <span className="gray text-xs font-semibold mb-2 ml-1">الكمية</span>
            <input
              type="number"
              className="w-10 h-6 border border-[#979797] rounded-sm"
            />
          </div>
          <div className="text-[#555550] font-semibold text-lg">30 $</div>
        </div>
      </div>
    </div>
  );
};

export default CartItem;
