import React from 'react';
import ProductCard from "./ProductCard";
import SubTitle from '../Uitlity/SubTitle';
import prod1 from "../../images/prod1.png";
import item from "../../images/item.png";

const CardProductsContainer = ({title, btntitle, pathText}) => {
  return (
    <div className="containerXl lg:max-[1100px]:px-6 md:max-[992px]:px-6 max-sm:px-6 col-span-11 max-md:col-span-10 max-md:px-6">
    <SubTitle title={title} btntitle={btntitle} pathText={pathText} />
        <div className="grid max-md:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
            <ProductCard img={prod1} />
            <ProductCard img={item} />
            <ProductCard img={prod1} />
            <ProductCard img={item} />
        </div>
    </div>
  )
}

export default CardProductsContainer
