import React from "react";
import UserAddressCard from "./UserAddressCard";
import { Link } from "react-router-dom";

const UserAllAddress = () => {
  return (
    <div className="">
      <div className="font-extrabold text-[22px] text-[#555550] mb-8">
        دفتر العناوين
      </div>
      <UserAddressCard />
      <UserAddressCard />
      <UserAddressCard />
      <Link to="/user/add-address" className="flex justify-center">
        <button className="duration-500 text-white hover:text-[#555550] bg-black p-3 rounded-lg">إضافة عنوان جديد</button>
      </Link>
    </div>
  );
};

export default UserAllAddress;
