import React from 'react'
import CartItem from '../../Components/Cart/CartItem'
import CartCheckout from '../../Components/Cart/CartCheckout'
// style={{minHeight:"670px"}} == h-screen ==> عشان الفوتر ينزل تحت وتتزبط الصفحة

const CartPage = () => {
  return (
    <div style={{minHeight:"670px"}} className="containerXl max-sm:px-6 mb-4">
      <div className="mt-6 mb-2 font-extrabold text-xl text-[#555550]">عربة التسوق</div>
      <div className="grid grid-cols-12 gap-4">
        <div className="col-span-9 max-sm:col-span-12 max-md:col-span-8">
            <CartItem />
            <CartItem />
            <CartItem />
            <CartItem />
        </div>
        <div className="col-span-3 max-sm:col-span-12 max-md:col-span-4">
            <CartCheckout />
        </div>
      </div>
    </div>
  )
}

export default CartPage
