import React from "react";
import ReactStars from "react-rating-stars-component";

const RatePost = () => {
  const setting = {
    size: 20,
    count: 5,
    color: "gray",
    activeColor: "#ffc107",
    value: 7.5,
    a11y: true,
    isHalf: true,
    emptyIcon: <i className="far fa-star" />,
    halfIcon: <i className="fa fa-star-half-alt" />,
    filledIcon: <i className="fa fa-star" />,
    onChange: (newValue) => {
      console.log(`Example 2: new value is ${newValue}`);
    },
  };
  return (
    <div className="mt-5 pb-2">
      <div className="flex items-center gap-4 mx-10">
        <p className="text-[#555550] text-sm font-bold">محمد الكرنز</p>
        <ReactStars {...setting} />
      </div>
      <div className="mx-10">
        <textarea
          cols="20"
          rows="3"
          placeholder="اكتب تعليقك..."
          className="w-full bg-[#f9f9f9] rounded-lg p-2 border border-[#979797] mt-3"></textarea>
      </div>
      <div className="border-b pb-2 flex justify-end">
        <div className="text-white hover:gray mx-10 bg-black border-[#979797] duration-500 cursor-pointer px-5 py-3 rounded-lg text-sm">أضف تعليق</div>
      </div>
    </div>
  );
};

export default RatePost;
